.education {
  padding: 60px 20px;
  max-width: 1100px;
  margin: 0 auto 60px;
  position: relative;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.title {
  position: relative;
  font-size: 2rem;
  margin-bottom: 40px;
  text-align: center;
  color: var(--secondary-color);
}

.title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: var(--primary-color);
}

.timeline {
  position: relative;
  padding: 20px 0;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 3px;
  margin-left: -1.5px;
  background: linear-gradient(
    to bottom,
    transparent,
    var(--primary-color),
    transparent
  );
  opacity: 0.4;
  box-shadow: 0 0 5px rgba(198, 40, 40, 0.2);
}

.educationItem {
  position: relative;
  margin-bottom: 40px;
  background-color: var(--bg-main);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  padding: 25px;
  width: calc(50% - 40px);
  box-sizing: border-box;
  transition: var(--transition);
  border-top: 3px solid var(--primary-color);
}

.educationItem:nth-child(odd) {
  float: left;
  clear: both;
}

.educationItem:nth-child(even) {
  float: right;
  clear: both;
}

.educationItem::before {
  content: "";
  position: absolute;
  top: 20px;
  width: 20px;
  height: 20px;
  background-color: var(--primary-color);
  border-radius: 50%;
  z-index: 1;
  box-shadow: 0 0 0 4px rgba(255, 171, 0, 0.2);
  transition: var(--transition);
}

.educationItem:nth-child(odd)::before {
  right: -50px;
}

.educationItem:nth-child(even)::before {
  left: -50px;
}

.educationItem:hover::before {
  transform: scale(1.2);
  box-shadow: 0 0 0 6px rgba(255, 171, 0, 0.3), 0 0 20px var(--primary-color);
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.institution {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 10px;
  color: var(--secondary-color);
}

.degree {
  font-size: 1.1rem;
  margin-bottom: 5px;
  color: var(--primary-color);
}

.dates {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-light);
  margin-bottom: 15px;
  display: inline-block;
  padding: 3px 10px;
  background-color: rgba(198, 40, 40, 0.1);
  border-radius: 12px;
}

.description {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-color);
}

@media (max-width: 768px) {
  .timeline::before {
    left: 20px;
  }
  .educationItem {
    width: calc(100% - 60px);
    float: right;
    margin-left: 60px;
  }
  .educationItem:nth-child(odd)::before,
  .educationItem:nth-child(even)::before {
    left: -40px;
  }
}
