/* Header.module.css */

.header {
  background: linear-gradient(135deg, var(--bg-dark), var(--primary-color));
  color: #fff;
  padding: 80px 0 60px;
  text-align: center;
  margin-bottom: 30px;
  box-shadow: var(--shadow);
  position: relative;
  overflow: hidden;
}

.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(
    circle at center,
    rgba(255, 255, 255, 0.2) 0%,
    transparent 70%
  );
  z-index: 1;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  position: relative;
  z-index: 2;
}

.arcReactor {
  width: 100px;
  height: 100px;
  margin: 0 auto 30px;
  position: relative;
}

.arcReactorOuter {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 4px solid rgba(255, 255, 255, 0.7);
  box-sizing: border-box;
  animation: pulse 2s infinite;
}

.arcReactorInner {
  position: absolute;
  top: 20%;
  left: 20%;
  width: 60%;
  height: 60%;
  background-color: var(--primary-color);
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(198, 40, 40, 0.5);
  opacity: 0.8;
}

.arcReactorCore {
  position: absolute;
  top: 35%;
  left: 35%;
  width: 30%;
  height: 30%;
  background-color: white;
  border-radius: 50%;
  box-shadow: 0 0 30px rgba(255, 255, 255, 0.9);
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4);
  }
  70% {
    box-shadow: 0 0 0 15px rgba(255, 255, 255, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
  }
}

.header h1 {
  font-size: 3em;
  font-weight: 700;
  margin-bottom: 10px;
  letter-spacing: -0.5px;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}

.header p {
  font-size: 1.3em;
  margin-bottom: 15px;
  opacity: 0.9;
}

.statusText {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.15);
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 1em;
  margin-top: 20px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);
}

.social {
  margin-top: 30px;
}

.social a {
  color: white;
  margin: 0 15px;
  opacity: 0.8;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);
}

.social a:hover {
  opacity: 1;
  transform: translateY(-3px);
  background: var(--accent-color);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}
