.projects {
  padding: 60px 20px;
  max-width: 1100px;
  margin: 0 auto 60px;
  position: relative;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.title {
  position: relative;
  font-size: 2rem;
  margin-bottom: 40px;
  text-align: center;
  color: var(--secondary-color);
}

.title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: var(--primary-color);
}

.projectsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 30px;
  padding: 20px 0;
}

.projectCard {
  background-color: var(--bg-main);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow);
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: var(--transition);
  border-left: 3px solid var(--primary-color);
  position: relative;
}

.projectCard::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  background: linear-gradient(
    135deg,
    transparent 50%,
    var(--primary-color) 51%
  );
  opacity: 0.6;
  z-index: 1;
}

.projectHeader {
  padding: 20px;
  border-bottom: 1px solid var(--bg-light);
  position: relative;
}

.projectName {
  font-size: 1.4rem;
  margin-bottom: 10px;
  color: var(--secondary-color);
  font-weight: 600;
}

.projectContent {
  padding: 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.projectDescription {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-color);
  margin-bottom: 20px;
  flex-grow: 1;
}

.techStack {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.techTag {
  font-size: 0.8rem;
  padding: 4px 10px;
  border-radius: 12px;
  background-color: var(--bg-light);
  color: var(--secondary-color);
  transition: var(--transition);
  border: 1px solid transparent;
}

.techTag:hover {
  transform: translateY(-2px);
  border-color: var(--primary-color);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.projectFooter {
  padding: 15px 20px;
  background-color: var(--bg-light);
  display: flex;
  justify-content: flex-end;
  position: relative;
  overflow: hidden;
}

.projectFooter::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 171, 0, 0.1),
    transparent
  );
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}

.projectCard:hover .projectFooter::before {
  transform: translateX(100%);
}

.githubLink {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: var(--transition);
  position: relative;
  z-index: 2;
}

.githubLink:hover {
  color: var(--secondary-color);
  transform: translateX(3px);
}

.ironEffect {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0 100px 100px;
  border-color: transparent transparent var(--primary-color) transparent;
  position: absolute;
  right: 0;
  bottom: 0;
  opacity: 0.1;
  pointer-events: none;
}

@media (max-width: 768px) {
  .projectsGrid {
    grid-template-columns: 1fr;
  }
}
