.footer {
  background-color: var(--bg-dark);
  color: white;
  padding: 40px 20px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(
    90deg,
    transparent,
    var(--primary-color),
    transparent
  );
  box-shadow: 0 0 20px rgba(198, 40, 40, 0.3);
}

.container {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.contact {
  margin-bottom: 20px;
}

.name {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 5px;
  color: var(--primary-color);
  text-shadow: 0 0 5px rgba(198, 40, 40, 0.3);
}

.email {
  font-size: 1.1rem;
  color: var(--primary-color);
  text-decoration: none;
  transition: var(--transition);
  opacity: 0.9;
}

.email:hover {
  color: white;
  text-shadow: 0 0 10px var(--accent-color);
}

.social {
  display: flex;
  gap: 20px;
  margin: 20px 0;
}

.socialLink {
  color: white;
  font-size: 1.4rem;
  opacity: 0.7;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
}

.socialLink:hover {
  opacity: 1;
  transform: translateY(-5px);
  background: var(--primary-color);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.copyright {
  margin-top: 30px;
  font-size: 0.9rem;
  opacity: 0.7;
}

.reactorIcon {
  width: 40px;
  height: 40px;
  margin: 0 auto 20px;
  position: relative;
}

.reactorOuter {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 2px solid var(--ironman-gold);
  box-sizing: border-box;
}

.reactorInner {
  position: absolute;
  top: 25%;
  left: 25%;
  width: 50%;
  height: 50%;
  background-color: var(--primary-color);
  border-radius: 50%;
  opacity: 0.8;
}

.reactorCore {
  position: absolute;
  top: 35%;
  left: 35%;
  width: 30%;
  height: 30%;
  background-color: white;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.9);
}
