.skills {
  padding: 60px 20px;
  background: var(--bg-main);
  max-width: 1100px;
  margin: 0 auto 60px;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  position: relative;
  overflow: hidden;
}

.skills::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 30%;
  height: 100%;
  background-image: radial-gradient(
    circle at right top,
    rgba(198, 40, 40, 0.05),
    transparent 70%
  );
  pointer-events: none;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 2;
}

.title {
  position: relative;
  font-size: 2rem;
  margin-bottom: 40px;
  text-align: center;
  color: var(--secondary-color);
}

.title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: var(--primary-color);
}

.skillsCloud {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  padding: 20px 0;
}

.skillTag {
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 1rem;
  font-weight: 500;
  background-color: white;
  box-shadow: var(--shadow);
  transition: var(--transition);
  cursor: default;
  position: relative;
  overflow: hidden;
}

.skillTag::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transform: translateX(-100%);
  transition: transform 0.5s;
}

.skillTag:hover::before {
  transform: translateX(100%);
}

.skillTag:hover {
  transform: translateY(-5px) rotate(2deg);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.primary {
  color: white;
  background-color: var(--primary-color);
}

.secondary {
  color: white;
  background-color: var(--secondary-color);
}

.accent {
  color: white;
  background-color: var(--accent-color);
}

.skill1 {
  background-color: var(--ironman-red);
  color: white;
}

.skill2 {
  background-color: var(--ironman-dark);
  color: white;
}

.skill3 {
  background-color: #616161;
  color: white;
}

.skill4 {
  background-color: #5a6268;
  color: white;
}

.skill5 {
  background-color: #808080;
  color: white;
}

@media (max-width: 768px) {
  .skills::before {
    width: 50%;
  }
}
