.about {
  padding: 60px 20px;
  background-color: var(--bg-main);
  box-shadow: var(--shadow);
  border-radius: var(--border-radius);
  max-width: 1100px;
  margin: 0 auto 60px;
  position: relative;
  overflow: hidden;
}

.about::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(
    to right,
    transparent,
    var(--primary-color),
    transparent
  );
  z-index: 1;
}

.about::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: linear-gradient(
    45deg,
    transparent 45%,
    var(--primary-color) 47%,
    transparent 50%
  );
  background-size: 10px 10px;
  opacity: 0.05;
  z-index: 1;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.title {
  position: relative;
  font-size: 2rem;
  margin-bottom: 40px;
  text-align: center;
  color: var(--secondary-color);
}

.title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: var(--primary-color);
}

.content {
  font-size: 1.1rem;
  line-height: 1.8;
  color: var(--text-color);
  text-align: justify;
  padding: 10px;
  border-radius: var(--border-radius);
  position: relative;
}

.highlight {
  color: var(--primary-color);
  font-weight: 600;
  transition: var(--transition);
  position: relative;
  display: inline-block;
}

.highlight::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--primary-color);
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
  transform-origin: left;
}

.highlight:hover {
  text-shadow: 0 0 8px rgba(198, 40, 40, 0.3);
}

.highlight:hover::after {
  transform: scaleX(1);
}
