.app {
  font-family: "Inter", "Roboto", "Helvetica Neue", sans-serif;
  line-height: 1.6;
  transition: all 0.3s ease;
}

:root {
  /* Iron Man theme colors */
  --ironman-red: #c62828;
  --ironman-gold: #ffab00;
  --ironman-hover: #ef6c00;
  --ironman-light: #f5f5f5;
  --ironman-dark: #212121;
}

.light {
  --primary-color: var(--ironman-red);
  --secondary-color: #424242;
  --accent-color: #9e9e9e;
  --text-color: #2c3e50;
  --text-light: #7f8c8d;
  --bg-light: #ecf0f1;
  --bg-dark: #212121;
  --bg-main: #f8f9fa;
  --border-radius: 4px;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
  background-color: var(--bg-main);
  color: var(--text-color);
}

.dark {
  --primary-color: var(--ironman-red);
  --secondary-color: #9e9e9e;
  --accent-color: #757575;
  --text-color: #e0e0e0;
  --text-light: #bdbdbd;
  --bg-light: #424242;
  --bg-dark: #212121;
  --bg-main: #303030;
  --border-radius: 4px;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  --transition: all 0.3s ease;
  background-color: var(--bg-main);
  color: var(--text-color);
}

.section {
  padding: 60px 20px;
  max-width: 1100px;
  margin: 0 auto;
}

.section-title {
  position: relative;
  font-size: 2rem;
  margin-bottom: 40px;
  text-align: center;
  color: var(--secondary-color);
}

.section-title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: var(--primary-color);
}

.scrollToTop {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--primary-color);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 100;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  border: none;
  transition: all 0.3s ease;
}

.scrollToTop:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}

.themeToggle {
  position: fixed;
  bottom: 30px;
  left: 30px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--bg-dark);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 100;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  border: 2px solid var(--primary-color);
  transition: all 0.3s ease;
}

.themeToggle:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}
